<template>
  <div class="post">
    <div class="container">
      <div class="card">
        <h2>레시피 작성</h2>
        <span>타이틀</span>
        <input type="text" name="" v-model="title" id="">
        <span>서브타이틀</span>
        <input type="text" name="" v-model="subTitle" id="">
        <div class="imageUploadContainer">
          <button class="image">
            <label for="file" class="button"><span>이미지 선택</span></label>
          </button>
          <input type="file" id="file" class="hidden" @change="fileChnage($event, -1)">
          <img class="preview" :src="titleImageBase64" />
        </div>
      </div>

      <div class="card" v-for="(action, index) in recipes" :key="index">
        <h2>{{ index + 1 }}번째</h2>
        <span>설명</span>
        <textarea name="" id="" v-model="recipes[index].text"/>
        <div class="imageUploadContainer">
          <button class="image">
            <label :for="'file'+index" class="button"><span>이미지 선택</span></label>
          </button>
          <input type="file" :id="'file'+index" class="hidden" @change="fileChnage($event, index)">
          <img class="preview" :src="recipes[index].titleImageBase64" />
        </div>
      </div>

      <div class="card uploadButtonContainer">
        <button @click="addAction()">칸 추가</button>
        <button @click="uploadPost()">업로드</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Post',
  components: {

  },
  data () {
    return {
      recipes: [{
        text: "",
        titleImage: "",
        titleImageBase64: "none"
      }],
      title: "",
      subTitle: "",
      titleImage: "",
      titleImageBase64: "none"
    }
  },
  mounted() {
    if (sessionStorage.token == null || sessionStorage.token == "") {
      this.$router.push("/login");
    }
  },
  methods: {
    uploadPost() {
      let token = sessionStorage.token
      let formData = new FormData();
      formData.append("token", token);
      formData.append("title", this.title);
      formData.append("subTitle", this.subTitle);
      formData.append("image", this.titleImage);
      this.axios.post('http://recipeapp.saintdev.kr:28080/api/post/create', formData, {
        headers: {
          'content-type': 'multipart/form-data'
        }
      }).then(res => {
        let postId = res.data.body.postId;
        let requestPromises = [];
        let partFormData;
        for (let i=0; i<this.recipes.length; i++) {
          partFormData = new FormData();
          partFormData.append("token", token);
          partFormData.append("description", this.recipes[i].text);
          partFormData.append("postId", postId);
          partFormData.append("partId", i+1);
          partFormData.append("image", this.recipes[i].titleImage);
          requestPromises.push(this.axios.post('http://recipeapp.saintdev.kr:28080/api/post/createPart', partFormData, {
            headers: {
              'content-type': 'multipart/form-data'
            }
          }));
        }
        Promise.all(requestPromises).then(() => {
          alert("완료되었습니다.");
          this.$router.push("/");
        });
      }).catch(err => {
        alert("모두 작성후 업로드해주세요");
      });
    },
    async fileChnage(event, number) {
      if (number == -1) {
        this.titleImage = event.target.files[0];        
        let dataUrl = await new Promise(resolve => {
          let reader = new FileReader();
          reader.onload = () => resolve(reader.result);
          reader.readAsDataURL(this.titleImage);
        });
        this.titleImageBase64 = dataUrl;
      } else {
        this.recipes[number].titleImage = event.target.files[0];
        let dataUrl = await new Promise(resolve => {
          let reader = new FileReader();
          reader.onload = () => resolve(reader.result);
          reader.readAsDataURL(this.recipes[number].titleImage);
        });
        this.recipes[number].titleImageBase64 = dataUrl;
      }
    },
    addAction() {
      this.recipes.push({
        text: "",
        titleImage: "",
        titleImageBase64: "none"
      });
    }
  }
}
</script>

