<template>
  <div class="read">
    <div class="container">
      <div>
          <div class="card" id="title">
              <div class="buttonLine">
                <button v-if="recipe.isWriter == true" @click="deletePost()">삭제</button><button v-if="recipe.isWriter == true" @click="editPost()">수정</button><button @click="createBookmark()" v-if="recipe.isBookmark == false">즐겨찾기 등록</button><button @click="deleteBookmark()" v-else>즐겨찾기 해제</button>
              </div>
              <h1>{{ recipe.title }}</h1>
              <h3>{{ recipe.subTitle }}</h3>
              <hr>
              <img :src="recipe.image">
          </div>
      </div>
      <div v-for="action in recipe.actions" :key="action.partNumber">
          <div class="card action">
              <img :src="action.image">
              <h2>{{ action.partNumber }}</h2>
              <p>{{ action.text }}</p>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Read',
  components: {

  },
  data() {
    return {
      recipe: {}
    }
  },
  mounted() {
    this.axios.post('http://recipeapp.saintdev.kr:28080/api/post/readOne', {
      postId: this.$route.params.id, token: sessionStorage.token
    }).then(res => {
      let recipe = res.data.body.recipe;
      this.actions = recipe.actions;
      this.recipe = recipe;
    }).catch(err => {
      console.error(err);
    });
  },
  methods: {
    deletePost() {
      if (confirm("게시글을 삭제하시겠습니까?")) {
        this.axios.delete('http://recipeapp.saintdev.kr:28080/api/post/remove', {data: {
          postId: this.$route.params.id, token: sessionStorage.token
        }}).then(res => {
          this.$router.push("/");
        }).catch(err => {
          console.error(err);
        });
      }
    },
    editPost() {
      this.$router.push("/edit/"+this.$route.params.id);
    },
    createBookmark() {
      if (sessionStorage.token == null || sessionStorage.token == "") {
        this.$router.push("/login");
        return;
      }

      if (confirm("즐겨찾기에 추가하시겠습니까?")) {
        this.axios.post('http://recipeapp.saintdev.kr:28080/api/bookmark/create', {
          postId: this.$route.params.id, token: sessionStorage.token
        }).then(res => {
          this.recipe.isBookmark = true;
        }).catch(err => {
          console.error(err);
        });
      }
    },
    deleteBookmark() {
      if (sessionStorage.token == null || sessionStorage.token == "") {
        this.$router.push("/login");
        return;
      }

      if (confirm("즐겨찾기를 삭제하시겠습니까?")) {
        this.axios.post('http://recipeapp.saintdev.kr:28080/api/bookmark/remove', {
          postId: this.$route.params.id, token: sessionStorage.token
        }).then(res => {
          this.recipe.isBookmark = false;
        }).catch(err => {
          console.error(err);
        });
      }
    }
  }
}
</script>

