<template>
  <div class="main">
    <div v-for="recipe in recipes" :key="recipe.id" class="card" @click="clickToRead(recipe.id)">
      <img :src="recipe.image">
      <h2>{{ recipe.title }}</h2>
      <span>작성: {{ recipe.writer }}</span>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'Main',
  components: {

  },
  data () {
    return {
      recipes: []
    }
  },
  mounted() {
    this.axios.get('http://recipeapp.saintdev.kr:28080/api/post/readAll').then(res => {
        for (let recipe of res.data.body.result) {
          if (recipe != null) {
            this.recipes.push(recipe);
          }
        }
    }).catch(err => {
        console.error(err);
    });
  },
  methods: {
    clickToRead(number) {
      this.$router.push('/read/'+number);
    }
  }
}
</script>

