<template>
  <div class="login">
    <div class="card">
      <h2>모두의 레시피</h2>
      <span>이메일</span>
      <input type="text" v-model="email">
      <span>비밀번호</span>
      <input type="password" v-model="password">
      <button @click="login()">로그인</button>
      <router-link to="/register">계정이 없으신가요?</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  components: {

  },
  data () {
    return {
      email: "",
      password: ""
    }
  },
  methods: {
    login() {
        this.axios.post('http://recipeapp.saintdev.kr:28080/api/auth/login', {
          email: this.email, password: this.password
        }).then(res => {
          let token = res.data.body.token;
          sessionStorage.setItem("token", token);
          this.$router.push("/");
        }).catch(err => {
            if (err.response.status == 404) {
              alert("잘못된 이메일 또는 비밀번호입니다.");
            }
        });
    }
  }
}
</script>

