<template>
  <div class="login">
    <div class="card">
      <h2>모두의 레시피</h2>
      <span>이메일</span>
      <input type="text" v-model="email">
      <span>비밀번호</span>
      <input type="password" v-model="password">
      <span>닉네임</span>
      <input type="text" v-model="nickname">
      <button @click="register()">회원가입</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Register',
  components: {

  },
  data () {
    return {
      email: "",
      password: "",
      nickname: ""
    }
  },
  methods: {
    register() {
        this.axios.post('http://recipeapp.saintdev.kr:28080/api/auth/register', {
          email: this.email, password: this.password, nickname: this.nickname
        }).then(res => {
          let token = res.data.body.token;
          sessionStorage.setItem("token", token);
          this.$router.push("/login");
        }).catch(err => {
          alert("잘못된 회원가입입니다.");
        });
    }
  }
}
</script>

